@import url("https://fonts.googleapis.com/css2?family=Poppins:wght@400;500;600;700;800;900&display=swap");

* {
	font-family: "Poppins", sans-serif;
}

body {
	margin: 0;
	background: linear-gradient(180deg, #242435 0%, #100f1b 100%);
	background-repeat: no-repeat;
	color: "white";
}

.root {
	display: flex;
	justify-content: center;
	align-items: center;

	position: absolute;
	top: 0;
	left: 0;
	width: 100vw;
	height: 100vh;
}

.title {
	font-size: 10vw;
	color: white;
}

canvas {
	width: 100%;
}
